// based on reference polyfill on MDN:
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Object/assign#Polyfill
export function createAssignPolyfill(): void {
  if (typeof (Object as any).assign !== 'function') { // tslint:disable-line:no-any
    Object.defineProperty(Object, 'assign', {
      value: (target: any, ...args: any[]) => { // tslint:disable-line:no-any
        if (!target) {
          throw new TypeError('Cannot convert undefined or null to object');
        }
        const to: Object = Object(target);
        for (const nextSource of args) {
          if (nextSource) {
            for (const nextKey in nextSource) {
              if (nextSource.hasOwnProperty(nextKey)) {
                to[nextKey] = nextSource[nextKey];
              }
            }
          }
        }
        return to;
      },
      writable: true,
      configurable: true
    });
  }
}